<template>
  <div class="loginBox">
    <div class="login">
        <el-form ref="form" style="margin-top: 50px">
          <el-form-item label="手机号：" label-width="150px">
            <el-input v-model="form.mobile" placeholder="请输入手机号"></el-input>
          </el-form-item>
          <el-form-item label="邮箱：" label-width="150px">
            <el-input v-model="form.email" placeholder="请输入邮箱"></el-input>
          </el-form-item>
          <el-form-item label="密码：" label-width="150px">
            <el-input type="password" v-model="form.password" placeholder="请输入密码"></el-input>
          </el-form-item>
          <el-form-item label="确认密码：" label-width="150px">
            <el-input type="password" v-model="rePassword" placeholder="请输入确认密码"></el-input>
          </el-form-item>
          <el-form-item label="姓名：" label-width="150px">
            <el-input v-model="form.name" placeholder="请输入姓名"></el-input>
          </el-form-item>
          <el-form-item label="性别：" label-width="150px">
            <el-radio-group v-model="form.sex">
              <el-radio label="男"></el-radio>
              <el-radio label="女"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="部门：" label-width="150px">
            <el-input v-model="form.note" placeholder="请备注部门"></el-input>
          </el-form-item>
        </el-form>
      <div style="text-align: right;margin-right: 20%">
        <el-button size="mini" type="primary" @click="register">注册</el-button>
      </div>
    </div>
  </div>
</template>
<script>
    export default {
        data() {
            return {
                showNav: false,
                rePassword: '',
                form: {
                    name: '',
                    password: '',
                    note: '',
                    email: '',
                    sex: '男',
                    mobile: ''
                }
            }
        },
        methods: {
            register(){
                if (this.form.mobile === '') {
                    this.$message.error('手机号不能为空');
                    return
                }
                if (this.form.email === '') {
                    this.$message.error('邮箱不能为空');
                    return
                }
                if (this.form.password === '') {
                    this.$message.error('密码不能为空');
                    return
                }
                if (this.form.name === '') {
                    this.$message.error('用户名不能为空');
                    return
                }
                if (this.form.department_id === '') {
                    this.$message.error('请选择部门')
                }
                if (this.rePassword !== this.form.password) {
                    this.$message.error('两次密码不一致');
                    return false
                }
                if (this.form.sex === '男') {
                    this.form.sex = '1'
                } else {
                    this.form.sex = '0'
                }
                this.$post('/signUp',
                    this.form
                ).then(() => {
                    this.$router.push({
                        path: '/'
                    })
                }).catch(err => {
                    this.$message.error(err)
                });
            }
        }
    }
</script>

<style lang="scss" scoped>
  .loginBox{
    text-align: center;
    margin: 0;
    padding: 0;
    height: 100%;
    width: 100%;
    background-color: #25a4bb;
    position: relative;
    background-image: url("../../../../static/img/bjImg.png");
    .login{
      padding: 15px;
      width: 800px;
      margin: 0 auto;
      background-color: #fff;
      position: absolute;
      left:50%;
      top:50%;
      transform: translate(-50%, -50%);
      .el-input, .el-select{
        width: 80%;
      }
      .el-button{
        width: 15%;
        font-size: 18px;
      }
    }
  }
  /deep/ .el-form-item .el-form-item__content{
    text-align: left;
  }
</style>



